<template>
  <div class="login">
    <div class="topbar">
      <h1 class="logo">
        <img src="../assets/img/common/logo.png" alt>导音
      </h1>
      <div class="login" @click="openWindow">登陆</div>
    </div>
    <div class="container">
      <div class="content">
        <h2>看视频，读文章，发现好老师</h2>
        <div class="center">
          <div class="left">
            <img src="../assets/img/login/1.png" alt>
            <img src="../assets/img/login/2.png" alt>
            <img src="../assets/img/login/3.png" alt>
          </div>
          <div class="right">
            <div class="imgbox">
              <img src="../assets/img/login/weixin.png" alt>
            </div>
            <p class="button">ios下载</p>
            <p class="button">android下载</p>
          </div>
        </div>
      </div>
    </div>
    <p class="ip">北京导音教育科技有限公司 | 北京市房山区康泽路3号院12号楼6层2单元602 | 京ICP备18058516号-1</p>
    <div class="mask" v-if="showLogin"></div>
    <div class="loginIn" v-if="showLogin">
      <div class="box" v-if="loginType==1">
        <p>欢迎登陆</p>
        <div class="content">
          <div id="wx_ewm"></div>
          <!-- <img src="../assets/img/login/weixin.png" alt> -->
          <p>微信扫一扫立即登陆</p>
        </div>
        <p class="scan">
          <span @click="changeLoginType(2)">QQ扫一扫</span>
          <span @click="changeLoginType(3)">手机登陆</span>
        </p>
        <span class="close" @click="closeWindow()">X</span>
      </div>
      <div class="box" v-else-if="loginType==2">
        <p>欢迎登陆</p>
        <div class="content">
          <img src="../assets/img/login/weixin.png" alt>
          <p>QQ扫一扫立即登陆</p>
        </div>
        <p class="scan">
          <span @click="changeLoginType(1)">微信扫一扫</span>
          <span @click="changeLoginType(3)">手机登陆</span>
        </p>
        <span class="close" @click="closeWindow()">X</span>
      </div>
      <div class="box phonebox" v-else-if="loginType==3">
        <p>欢迎登陆</p>
        <div class="content">
          <p>
            <span>手机号:</span>
            <input v-model.number="phone" maxlength="11">
          </p>
          <p>
            <span>密&nbsp;&nbsp;&nbsp;码:</span>
            <input type="password" v-model="pwd">
          </p>
          <p @click="loginIn">登陆</p>
          <p>如忘记密码通过手机端重置</p>
        </div>
        <p class="scan">
          <span @click="changeLoginType(2)">QQ扫一扫</span>
          <span @click="changeLoginType(1)">微信扫一扫</span>
        </p>
        <span class="close" @click="closeWindow()">X</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: "login",
  data() {
    return {
      phone: "",
      pwd: "",
      loginType: 1,
      showLogin: false
    };
  },
  methods: {
    ...mapMutations(["saveLoginInfo"]),
    loginIn() {
      let phoneReg = /^1[3|4|5|6|7|8|9][0-9]\d{4,8}$/;
      if (this.phone == "") {
        alert("请输入手机号");
      } else if (
        (this.phone.length > 0 && this.phone.length < 11) ||
        !phoneReg.test(this.phone)
      ) {
        alert("请输入格式正确的手机号");
      } else if (this.pwd == "") {
        alert("请输入密码");
      } else if (this.pwd.length < 6) {
        alert("密码长度不符");
      } else {
        this.login(this.phone, this.pwd);
      }
    },
    login(phone, pwd) {
      this.loading = true;
      this.$http({
        url: "user/phone_pwd_login",
        data: { phone, pwd }
      })
        .then(({ res, ok }) => {
          if (ok) {
            localStorage.setItem(
              "user",
              JSON.stringify({
                lasttime: new Date().getTime(),
                info: escape(phone + "=" + pwd)
              })
            );
            this.saveLoginInfo({
              isLogin: true,
              phone,
              type: res.type,
              name: res.name
            });
            this.$router.replace({ path: "/home" });
          } else {
            alert(res);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    changeLoginType(index) {
      this.loginType = index;
      if (index == 1) {
        setTimeout(() => {
          let obj = new WxLogin({
            self_redirect: false,
            id: "wx_ewm",
            appid: "wxc203972c94033e0a",
            scope: "snsapi_login",
            redirect_uri: "https://www.daoyinjiaoyu.com",
            state: new Date().getTime(),
            style: "black",
            href:
              "data:text/css;base64,"
          });
          console.log($("iframe"));
          $("iframe").attr({ width: "210px", height: "156px" });
          console.log($("iframe").attr("width"));
        }, 1000);
        setTimeout(() => {}, 1100);
      }
    },
    closeWindow() {
      this.showLogin = false;
    },
    openWindow() {
      (this.loginType = 3), (this.showLogin = true);
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  height: 100%;
  position: relative;
  .topbar {
    position: absolute;
    width: 100%;
    height: 74px;
    top: 0;
    left: 0;
    padding: 0 18px 0 15px;
    line-height: 74px;
    color: #00b80c;
    display: flex;
    font-size: 24px;
    justify-content: space-between;
    .logo {
      font-size: 26px;
      img {
        vertical-align: middle;
        width: 50px;
        margin-right: 10px;
      }
    }
  }
  .container {
    height: 100%;
    padding-top: 121px;
    .content {
      height: 100%;
      display: flex;
      flex-direction: column;
      h2 {
        color: #171717;
        font-size: 50px;
      }
      .center {
        margin: 0 auto;
        width: 960px;
        height: 100%;

        display: flex;
        .left {
          height: 100%;
          width: 100%;
          padding-top: 60px;

          position: relative;
          img {
            position: absolute;
            left: 0;
            top: 77px;
            height: 448px;
            &:nth-child(2) {
              position: absolute;
              left: 92px;
              height: 482px;
              z-index: 10;
              top: 60px;
            }
            &:nth-child(3) {
              left: 217px;
            }
          }
        }
        .right {
          height: 100%;
          min-width: 250px;
          padding-top: 70px;
          .imgbox {
            height: 250px;
            line-height: 250px;
            text-align: center;
            width: 100%;
            border-radius: 23px;
            border: 1px solid rgba(112, 112, 112, 1);
            img {
              width: 205px;
              height: 205px;
              vertical-align: middle;
            }
          }
          .button {
            width: 254px;
            height: 57px;
            margin-top: 30px;
            border-radius: 32px;
            font-size: 24px;
            color: #fff;
            line-height: 57px;
            &:nth-child(2) {
              background: url("../assets/img/login/IOS.png") #09bb07 no-repeat
                left center;
              background-size: 32px;
              background-position: 27px center;
              padding-left: 70px;
            }
            &:nth-child(3) {
              background: url("../assets/img/login/android.png") #09bb07
                no-repeat;
              background-size: 32px;
              padding-left: 70px;
              background-position: 27px center;
            }
          }
        }
      }
    }
  }
  .ip {
    position: absolute;
    width: 100%;
    bottom: 10px;
    font-size: 18px;
  }
  .mask {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0.3;
    z-index: 100;
    display: flex;
    align-items: center;
  }
  .loginIn {
    position: absolute;
    z-index: 111;
    top: 50%;
    margin-top: -176px;
    height: 352px;
    width: 100%;
    text-align: center;
    .box {
      background: #fff;
      position: relative;
      display: inline-block;
      height: 100%;
      width: 296px;
      p {
        height: 50px;
        line-height: 50px;
        color: #333333;
        font-size: 18px;
      }
      .content {
        padding-top: 24px;
        margin: 0 auto;
        width: 210px;
        height: 237px;
        background: rgba(240, 240, 240, 1);
        #wx_ewm {
          width: 156px;
          height: 156px;
        }
        img {
          width: 156px;
          height: 156px;
        }
        p {
          margin-top: 10px;
          font-size: 16px;
        }
      }
      .scan {
        color: #09bb07;
        font-size: 16px;
        display: flex;
        padding: 0 43px 0;
        margin-top: 12px;
        justify-content: space-between;
      }
      .close {
        position: absolute;
        top: 16px;
        color: #000;
        font-size: 18px;
        right: 16px;
      }
    }
    .phonebox {
      .content {
        p {
          display: flex;
          justify-content: flex-start;
          width: 100%;
          height: 24px;
          padding: 0 0 0 10px;
          span {
            display: inline-block;
            width: 60px;
            min-width: 60px;
            text-align: right;
            height: 24px;
            line-height: 24px;
            vertical-align: middle;
          }
          input {
            margin-left: 8px;
            height: 24px;
            width: 112px;
            padding-left: 5px;
            border: 1px solid #707070;
            border-radius: 4px;
            background: #fff;
          }
          &:nth-child(3) {
            display: inline-block;
            background: #09bb07;
            font-size: 14px;
            margin-top: 22px;
            line-height: 32px;
            height: 32px;
            font-size: 18px;
            letter-spacing: 10px;
            color: #fff;
            border-radius: 4px;
            width: 80%;
            margin-top: 30px;
          }
          &:nth-child(4) {
            display: inline-block;
            font-size: 14px;
            margin-top: 22px;
            line-height: 26px;
            color: #9b9b9b;
            border-radius: 4px;
            width: 90%;
            margin-top: 55px;
          }
        }
      }
    }
  }
}
</style>




