import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
import Home from '@/components/home/home'
import ClassRoom from '@/components/home/pageview/classroom'
import EditPaper from '@/components/home/pageview/editpaper'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'login',
      component: Login,

    },
    {
      path: '/home',
      name: 'home',
      redirect: '/home/classroom',
      component: Home,
      children: [

        {
          path: 'classroom',
          name: "classroom",
          component: ClassRoom,
        },
        {
          path: 'editpaper',
          name: "editpaper",
          component: EditPaper,
        },

      ]

    }

  ]
})
router.beforeEach((to, from, next) => {

  if (to.name != 'login') {
    if (sessionStorage.getItem('isLogin')) {
      next()
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    let code = undefined;
    let reg = new RegExp("(^|&)" + 'code' + "=([^&]*)(&|$)");
    let r = window.location.search.substr(1).match(reg);
    if (r != null) {
      code = unescape(r[2]);
    } else {
      code = undefined;
    }
    if (code) {
alert("获取code:"+code)
      Vue.prototype.$http({
        url: "/user/login",
        data: { unionid: code, type: 2, platform: 1 }
      })
        .then(({ res, ok }) => {
          if (ok) {
    
            this.saveLoginInfo({
              isLogin: true,
              phone:res.phone,
              type: res.type,
              name: res.name
            });
            next('/home')
          } else {
            alert(res);
          }
        })
        .catch(error => {
          console.log(error);
        });

    } else {
      next()
    }

  }
})

export default router



