<template>
  <el-container class="editpaper">
    <el-main>
      <div class="container" ref="container">
        <el-input placeholder="输入文字标题(5-30个字）" v-model="title" maxlength="30" clearable></el-input>
        <div class="component_edit" :style="{height:height+'px'}">
          <textarea id="editor1" cols="30" rows="10" class="textarea"></textarea>
        </div>
        <div class="bottom">
          <div class="left">
            <div class="operate">
              <span>封面:</span>
              <el-radio-group v-model="radio" @change="change">
                <el-radio :label="3">自动</el-radio>
                <el-radio :label="1">单图</el-radio>
                <el-radio :label="2">三图</el-radio>
              </el-radio-group>
              <div class="selection">
                <span>阅读对象:</span>
                <el-select v-model="target" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.id"
                    :label="item.type"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </div>
            </div>
            <div class="previewimgbox" v-if="radio!=3">
              <img v-for="(item,index) in displayList " :key="index" :src="item" alt>
              <div
                @click="choseImg()"
                v-show="(radio==1&&displayList.length==0)||(radio==2&&displayList.length<3)"
              ></div>
            </div>
          </div>
          <div class="hideImg" style="display: none"></div>
          <div class="btnbox">
            <el-button type="success" @click="preView">预览</el-button>
            <el-button type="success" @click="publish">发布</el-button>
          </div>
        </div>
      </div>
    </el-main>
    <div class="mask" v-if="showBox"></div>
    <div class="imgechosoe" v-if="showBox">
      <p>
        <span>正文图片</span>
        <span @click="closeWindow">X</span>
      </p>
      <div class="imgbox">
        <!--  -->
        <div
          class="imgcontainer"
          @click="choiceimg(index)"
          v-for="(item,index) in imgList"
          :key="index"
        >
          <img class="img" :src="item" alt>
          <img
            class="right"
            v-show="resultIndex.includes(index)"
            src="../../../assets/img/editpaper/right.png"
          >
        </div>
      </div>
      <div class="btnbox1">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="sure">确认</el-button>
      </div>
    </div>
    <div class="videobox" v-if="showPhone">
      <div class="content">
        <h3>{{title}}</h3>
        <div class="desc">
          <img src="../../../assets/img/demo.jpg" alt>
          <p>
            <span>{{name}}</span>
            <span>{{new Date().getTime()|formatTime}}</span>
          </p>
        </div>
        <div id="article" v-html="content"></div>
      </div>
    </div>
    <div class="mask1" v-if="showPhone">
      <img @click="closeWindow" class="closevideo" src="../../../assets/img/close.png" alt>
    </div>
  </el-container>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      title: "",
      radio: 3,
      target: "",
      height: 0,
      imgList: [],
      resultIndex: [],
      displayList: [],
      showBox: false,
      content: "",
      showPhone: false,
      options: []
    };
  },
  computed: {
    ...mapState(["name"])
  },
  filters: {
    formatTime(value) {
      let date = new Date(value);
      console.log(date);
      let Y = 1900 + date.getYear();
      let M = "0" + (date.getMonth() + 1);
      let D = "0" + date.getDate();
      let h = "0" + date.getHours();
      let m = "0" + date.getMinutes();
      let s = "0" + date.getSeconds();
      return `${Y}-${M.substring(M.length - 2, M.length)}-${D.substring(
        D.length - 2,
        D.length
      )} ${h.substring(h.length - 2, h.length)}:${m.substring(
        m.length - 2,
        m.length
      )}:${s.substring(s.length - 2, s.length)}`;
    }
  },
  mounted() {
    this.getHeight();
    this.getClassList();
    setTimeout(() => {
      CKEDITOR.replace("editor1", {
        extraPlugins: ["easyimage", "autogrow"],
        removePlugins: "image",
        language: "zh-cn",
        toolbarCanCollapse: false,
        removeDialogTabs: "link:advanced",
        toolbar: [
          { name: "document", items: ["Undo", "Redo"] },
          //        { name: 'styles', items: [ 'Format' ] },
          //        { name: 'basicstyles', items: [ 'Bold', 'Italic', 'Strike', '-', 'RemoveFormat' ] },
          { name: "basicstyles", items: ["Bold"] },
          //        { name: 'paragraph', items: [ 'NumberedList', 'BulletedList' ] },
          { name: "insert", items: ["EasyImageUpload"] }
        ],
        imageStyleFormat: {
          title: "Full size image",
          className: "image-full-size"
        },
        resize_maxHeight: this.height,
        resize_minWidth: this.height,
        height: this.height,
        // resize_maxHeight: 500,
        // resize_minWidth: 500,
        // height: 500,
        toolbarLocation: "top",
        removePlugins: "elementspath",
        cloudServices_uploadUrl: "https://daoyinjiaoyu.com/edit/upload",
        cloudServices_tokenUrl:
          "https://33333.cke-cs.com/token/dev/ijrDsqFix838Gh3wGO3F77FSW94BwcLXprJ4APSp3XQ26xsUHTi0jcb1hoBt"
      });
    }, 1000);
  },

  methods: {
    change() {
      this.displayList = [];
    },
    sure() {
      this.displayList = [];
      this.resultIndex.forEach(item => {
        this.displayList.push(this.imgList[item]);
      });
      this.showBox = false;
    },
    cancel() {
      this.showBox = false;
    },
    choiceimg(index) {
      if (this.radio == 1 && this.resultIndex.length == 1) {
        alert("单图模式下最多选择一张封面图片");
        return;
      }
      if (this.radio == 2 && this.resultIndex.length == 3) {
        alert("三图模式下最多选择三张封面图片");
        return;
      }
      let targetIndex = this.resultIndex.indexOf(index);
      if (targetIndex == -1) {
        this.resultIndex.push(index);
      } else {
        this.resultIndex.splice(targetIndex, 1);
      }
    },
    getHeight() {
      this.height = this.$refs.container.clientHeight - 205;
    },
    preView() {
      this.content = CKEDITOR.instances.editor1.getData();
      setTimeout(() => {
        $(".article").removeAttr("style");
      }, 2000);
      this.showPhone = true;
    },
    closeWindow() {
      this.showBox = false;
      this.showPhone = false;
    },
    getcontentImg() {
      let content = CKEDITOR.instances.editor1.getData();
      $(".hideImg").html(content);
      $(".hideImg img").each((index, ele) => {
        console.log($(ele).attr("src"));
        this.imgList[index] = $(ele).attr("src");
      });
    },
    choseImg() {
      this.showBox = true;
      this.getcontentImg();
    },
    publish() {
      this.content = CKEDITOR.instances.editor1.getData();
      if (this.title == "") {
        alert("请输入文章标题");
        return;
      } else if (this.title.length < 5 || this.title.length > 30) {
        alert("标题长度5-30个字符");
        return;
      } else if (this.target == "") {
        alert("请选择阅读对象");
        return;
      } else if (this.radio == 2 && this.displayList.length != 2) {
        alert("请选择三张图片作为封面");
        return;
      } else if (this.radio == 1 && this.displayList.length != 1) {
        alert("请选择一张图片作为封面");
        return;
      } else {
        let data = {
          head: this.title,
          head_pic1: this.displayList[0] || "",
          head_pic2: this.displayList[1] || "",
          head_pic3: this.displayList[2] || "",
          subject_class: 0,
          grade_class: 0,
          subject: 0,
          grade: this.target,
          content: this.content
        };
        console.log(data);
        this.$http({
          url: "/article/add",

          data: data
        })
          .then(({ res, ok }) => {
            if (ok) {
              alert("恭喜您！文章发表成功，请耐心等待系统审核结果。");
              window.location.reload();
            } else {
              alert("文章发表失败，请重试！");
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    getClassList() {
      this.$http({
        url: "type/watch_type_list"
      })
        .then(({ res, ok }) => {
          if (ok) {
            this.options = res;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style lang="less" rel="stylesheet/stylus"  scoped>
.el-container {
  padding: 0;
  height: 100%;

  .el-main {
    padding: 0;
    width: 100%;

    padding: 5px 80px 0 0;
    .container {
      min-width: 630px;
      width: 60%;
      margin: 0 auto;
      border: 1px solid #8d8d8d;
      height: 100%;
      display: flex;
      flex-direction: column;
      .el-input {
        height: 40px;
        font-size: 18px;
      }
      .component_edit {
        margin-top: 2px;
        overflow-y: scroll;
        border-top: 1px solid #8d8d8d;
        border-bottom: 1px solid #8d8d8d;
        .textarea {
          width: 100%;
          font-size: 20px;
        }
      }
      .bottom {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #000;
        justify-content: space-between;
        .left {
          width: 450px;
          margin-left: 10px;
          display: flex;
          flex-direction: column;
          height: 170px;

          .operate {
            height: 40px;
            margin-top: 5px;
            display: flex;
            align-items: center;
            span {
              margin-right: 10px;
            }
            .el-radio-group {
              height: 11px;
              .el-radio + .el-radio {
                margin-left: 4px;
              }
            }
            .selection {
              margin-left: 50px;
              span {
                margin-right: 10px;
              }
              .el-select {
                height: 30px;
                width: 100px;
              }
            }
          }
          .previewimgbox {
            display: flex;

            div {
              background: url("../../../assets/img/editpaper/add.png") no-repeat
                center;
              background-size: 50px;
              margin-right: 33px;
              height: 120px;
              width: 120px;
            }
            img {
              height: 120px;
              width: 120px;
            }
          }
        }
        .btnbox {
          width: 100%;
          max-width: 180px;
          height: 160px;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-around;

          .el-button {
            width: 80%;
            max-width: 100px;
            margin-left: 0;
          }
        }
      }
    }
  }
  .mask {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0.3;
  }
  .imgechosoe {
    background: #fff;
    height: 420px;
    width: 500px;
    top: 50%;
    left: 50%;
    margin-left: -250px;
    margin-top: -210px;
    position: absolute;
    p {
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #333;
      font-size: 18px;
      padding-left: 10px;
      span:last-child {
        height: 100%;
        line-height: 40px;
        width: 40px;
      }
    }

    .imgbox {
      height: 320px;
      overflow-y: scroll;
      padding: 5px;
      display: flex;
      border-bottom: 1px solid #333;
      justify-content: space-between;
      flex-wrap: wrap;
      .imgcontainer {
        position: relative;
        height: 150px;
        width: 150px;

        .img {
          height: 150px;
          width: 150px;
          margin-bottom: 5px;
          position: absolute;
          z-index: 9;
          left: 0;
          top: 0;
        }
        .right {
          height: 30px;
          width: 30px;
          z-index: 10;
          position: absolute;
          right: 0;
        }
      }
    }
    .btnbox1 {
      margin-top: 10px;

      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;

      .el-button {
        margin-left: 24px;
      }
    }
  }
  .mask1 {
    height: 100%;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 80;
    opacity: 0.6;
    background: #000;
    img {
      position: absolute;
      right: 120px;
      top: 100px;
      width: 40px;
    }
  }
  .videobox {
    position: absolute;
    height: 656px;
    width: 316px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    //background: red;
    background: url("../../../assets/img/bgiphone.png") no-repeat center;
    background-size: 323px 658px;
    background-color: #000;
    border-radius: 55px;
    padding: 86px 17px 74px 16px;
    .content {
      background: #fff;
      height: 100%;
      padding-top: 5px;
      h3 {
        font-size: 17px;
        height: 23px;
      }
      .desc {
        margin-top: 10px;
        display: flex;
        justify-content: flex-end;
        img {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          overflow: hidden;
          margin-right: 5px;
        }
        p {
          display: flex;
          text-align: left;
          justify-content: flex-start;
          flex-direction: column;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>




